@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.audit-trail-wrapper {
  .logs-tab {
    display: flex;
    align-items: center;
    gap: 16px;
    position: relative;
    transition: left 0.3s ease;

    &::after {
      content: '';
      position: absolute;
      width: 72px;
      height: 6px;
      border-radius: 4px;
      background-color: pick-color('primary', 'web');
      bottom: -30%;
      left: 0.3px;
      transition: all 0.3s ease;
    }

    &.coc-selected::after {
      left: 90px;
      width: 120px;
    }

    .tab-item {
      color: pick-color('primary', 'blue');
      font-weight: 500;
      cursor: pointer;
      transition: color 0.3s ease;

      &.selected-tab {
        color: pick-color('primary', 'web');
      }
    }
  }

  .download-btn {
    display: flex;
    align-items: center;
    gap: 4px;
    background-color: pick-color('primary', 'web');
    border-radius: 50px;
    padding: 6px 20px;
    color: white;
    font-size: 14px;
    cursor: pointer;
  }

  .logs-container {
    padding-right: 2px;
    margin-top: 24px;
    max-height: 400px;
    overflow: hidden;
    overflow-y: scroll;

    .audit-log-item:not(:last-child) {
      margin-bottom: 24px;
    }

    .coc-log-item {
      padding: 16px;
      border: 1px solid color.adjust(pick-color('primary', 'blue'), $lightness: 48%);
      border-radius: 8px;
      box-shadow: 0 1px 3px color.adjust(pick-color('primary', 'blue'), $lightness: 48%);

      &:not(:last-child) {
        margin-bottom: 16px;
      }

      div {
        word-break: break-all;
      }
    }

    .item-username {
      font-size: 15px;
    }
  }
}
